import React, { useState } from "react";
import Accueil from "./components/Accueil/Accueil.js";
import Marche from "./components/Marche/Marche.js";
import "./App.css";

function App(props) {
    const [state, setState] = useState({ open: false });
    const [clear, setClear] = useState(false);

    function openApp() {
        setState({ open: true });
    }

    return (
        <div className={'page ' + (clear ? 'clear' : 'dark')}>
            {state.open ? (
                <Marche/>
            ) : (
                <div className="page accueil-wrapper" >
                    <div className='light-icon-wrapper'>
                    <i className={'light-icon clickable fa ' + (clear? 'fa-moon-o' : 'fa-sun-o')} onClick={() => setClear(!clear)}>O</i>
                    </div>
                    <Accueil callBack={openApp}/>
                </div>
            )}
        </div>
    );
}

export default App;
